import React from 'react'
import { useParams } from "react-router";
import { useSelector, useDispatch } from "react-redux";
import { fetchMeasurements } from '../actions';
import { useEffect, useState } from "react";
import '../CSS/RestaurantMeasurement.css';

const RestaurantMeasurements = () => {
    const [entrytime, setEntrytime] = useState('');
    const [exittime, setExittime] = useState('');

    const measurements = useSelector(state => state.measurements);
    const isProgressing = useSelector(state => state.isProgressing);
    const measurementsLoaded = useSelector(state => state.measurementsLoaded);

    const params = useParams();
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(fetchMeasurements(params.rest_id));
    }, [dispatch]);

    var measurementTimes = [];

    function calcDateDiff(d1, d2) {
        var date1 = new Date(d1);
        var date2 = new Date(d2);

        var diff = date2.getTime() - date1.getTime();

        var msec = diff;
        var hh = Math.floor(msec / 1000 / 60 / 60);
        msec -= hh * 1000 * 60 * 60;
        var mm = Math.floor(msec / 1000 / 60);

        var date_of_month = "" + date1.getDate();
        var day_of_week = date1.getDay();
        var month_of_year = date1.getMonth();

        const days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
        const months = ["January", "Febuary", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];

        var output = "" + days[day_of_week] + ", " + months[month_of_year] + " " + date_of_month + ": ";
        output += hh == 0 ? "" : hh + " hours, ";
        output += mm + " mins"
        measurementTimes.push((hh * 60) + mm);
        return output;
    }

    const handleSubmit = (e) => {
    
        e.preventDefault();
        if (entrytime == "" || exittime == "") {
            alert("Please complete the form before submitting.");
        }

        console.log(`Form submitted: ${entrytime}, ${exittime}`);    

    }

    return (
        <div>
            <div className="box">
                <h3>Submit your own measurement:</h3>
                <div className="innerbox">
                    <form onSubmit = {handleSubmit}>
                        <label for="entrytime">Time entered: </label>
                        <input onChange = {(e) => setEntrytime(e.target.value)} value = {entrytime} type="datetime-local" id="entrytime" name="entrytime"></input>
                        <label for="exittime">Time exited: </label>
                        <input onChange = {(e) => setExittime(e.target.value)} value = {exittime} type="datetime-local" id="exittime" name="exittime"></input>
                        <button type = 'submit'>Submit</button>
                    </form>
                </div>

            </div>
            <div className="box bottom-box">
                {isProgressing && <div className="spinner" />}
                {measurementsLoaded && measurements.results.length == 0 && <h2>There are no avaliable measurements for this location.</h2>}
                {measurementsLoaded && measurements.results.length > 0 && measurements.results.map(measurement => 
                    <h3>{calcDateDiff(measurement[2], measurement[3])}</h3>
                    )}
            </div>
            
            <a className="backButton" href={`/${params.chain_id}`}>Back</a>
            <a className="backButton" href={`/`}>Home</a>
        </div>
    )
}

export default RestaurantMeasurements
