import React from 'react'
import { useSelector, useDispatch } from "react-redux";
import { useParams } from "react-router";
import { fetchLocations } from '../actions';
import { useEffect } from "react";
import '../CSS/spinner.css';


const RestaurantDetailed = () => {
    const locations = useSelector(state => state.locations);
    const isProgressing = useSelector(state => state.isProgressing);
    const locationsLoaded = useSelector(state => state.locationsLoaded);

    const latitude = useSelector(state => state.latitude);
    const longitude = useSelector(state => state.longitude);

    const params = useParams();
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(fetchLocations(params.chain_id));
    }, [dispatch]);

    function refine_interval(interval, cd, mask) {
        if (cd&mask)
            interval[0] = (interval[0] + interval[1])/2;
      else
            interval[1] = (interval[0] + interval[1])/2;
    }

    function decodeGeoHash(geohash) {
        var is_even = 1;
        var lat = []; var lon = [];
        lat[0] = -90.0;  lat[1] = 90.0;
        lon[0] = -180.0; lon[1] = 180.0;
        var lat_err = 90.0;  var lon_err = 180.0;
        const BASE32 = "0123456789bcdefghjkmnpqrstuvwxyz";
        const BITS = [16, 8, 4, 2, 1];
        
        for (var i=0; i<geohash.length; i++) {
            var c = geohash[i];
            var cd = BASE32.indexOf(c);
            for (var j=0; j<5; j++) {
                var mask = BITS[j];
                if (is_even) {
                    lon_err /= 2;
                    refine_interval(lon, cd, mask);
                } else {
                    lat_err /= 2;
                    refine_interval(lat, cd, mask);
                }
                is_even = !is_even;
            }
        }
        lat[2] = (lat[0] + lat[1])/2;
        lon[2] = (lon[0] + lon[1])/2;
    
        return { latitude: lat, longitude: lon};
    }

    function calcCrow(lat1, lon1, lat2, lon2) 
    {
        lat1 = lat1[0];
        lon1 = lon1[0];
        if ((lat1 == lat2) && (lon1 == lon2)) {
            return 0;
        }
        else {
            var radlat1 = Math.PI * lat1/180;
            var radlat2 = Math.PI * lat2/180;
            var theta = lon1-lon2;
            var radtheta = Math.PI * theta/180;
            var dist = Math.sin(radlat1) * Math.sin(radlat2) + Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta);
            if (dist > 1) {
                dist = 1;
            }
            dist = Math.acos(dist);
            dist = dist * 180/Math.PI;
            dist = dist * 60 * 1.1515;
            return dist.toFixed(1);
        }
    }

    function getAddress(geohash) {
        const result = decodeGeoHash(geohash);
        var currentLat = (result.latitude[0]).toFixed(2);
        var currentLon = (result.longitude[0]).toFixed(2);
        fetch(`https://maps.googleapis.com/maps/api/geocode/json?latlng=${currentLat},${currentLon}&key=AIzaSyC-GnsYOSBo7-JcT7e83IQl9QKFuwVokD0`)
        .then(response => response.json())
        .then((data) => {
            document.getElementById(geohash).textContent=data.results[0].formatted_address;
        });
    }

    return (
        <div>
            {isProgressing && <div className="spinner" />}
            {locationsLoaded && locations.results.map(location => 
                    <a href={`${params.chain_id}/${location[0]}`} className="Restaurant-Component">
                        <h2>{calcCrow(decodeGeoHash(location[1]).latitude, decodeGeoHash(location[1]).longitude, latitude, longitude)} mi --&gt; <span id={location[1]}>{getAddress(location[1])}</span></h2>
                        <h3>View wait times</h3>
                    </a>
                )}
            <a className="backButton" href="/">Back</a>
        </div>
    )
}

export default RestaurantDetailed
