import React from 'react'
import { useEffect } from "react";
import {useSelector, useDispatch} from 'react-redux';
import { fetchChains } from '../actions';
import '../CSS/Restaurants.css';
import '../CSS/spinner.css';

const Restaurants = () => {
    const chains = useSelector(state => state.chains);  
    const chainLoaded = useSelector(state => state.chainLoaded);
    const isProgressing = useSelector(state => state.isProgressing);

    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(fetchChains());
    }, [dispatch]);

    return (
        <div>
            {isProgressing && <div className="spinner" />}
            {chainLoaded && chains.results.map(chain => 
                    <a href={`/${chain[0]}`} className="Restaurant-Component">
                        <h2>{chain[1]} &gt; <span className="Restaurant-Phone">{chain[2]}</span> </h2>
                        <h3>View locations</h3>
                    </a>
                )}
        </div>
    )
}

export default Restaurants
